import React, {useState} from "react";
import {addedArt, art} from "../interfaces";

interface props {
    handleAddArt: (art: art, file: addedArt) => void
}

const AddArt = ({handleAddArt}: props) => {
    const [file, setFile] = useState({} as addedArt);
    const [fileName, setFileName] = useState("");
    const [name, setName] = useState("");
    const [hebName, setHebName] = useState("");
    const [size, setSize] = useState("");

    const handleFile = async (file: HTMLInputElement & EventTarget) => {
        const contentFile = file.files?.length && file.files[0];
        if (contentFile) {
            const buffer = await contentFile.arrayBuffer();
            const uint = (new Uint8Array(buffer as ArrayBufferLike)) as unknown as number[];
            const fileName = URL.createObjectURL(contentFile)
            setFileName(fileName);
            setFile({file: btoa(String.fromCharCode(...uint)), name: contentFile.name});
        }
    }

    return <div className="addArt">
        <input type="file" accept="image/png,image/jpeg" onChange={(event) => handleFile((event.target))}/>
        <br/>
        <input
            id="name"
            type="text"
            value={name}
            onChange={(event) =>
                setName(event.target.value)}
        />
        <br/>
        <input
            id="heeName"
            type="text"
            value={hebName}
            onChange={(event) =>
                setHebName(event.target.value)}
        />
        <br/>
        <input
            id="size"
            type="text"
            value={size}
            onChange={(event) =>
                setSize(event.target.value)}
        />
        <br/>
        <button onClick={() => handleAddArt({file: fileName, name, hebName, size, isNew: true}, file)}>Add Art</button>
    </div>
};

export default AddArt;