import {addedArt, art} from "../interfaces";
import {Octokit} from "octokit";
import {decode, encode} from "js-base64"
import {READ_GITHUB_TOKEN, WRITE_GITHUB_TOKEN} from "../config";

const write = new Octokit({
    auth: WRITE_GITHUB_TOKEN
})

const read = new Octokit({
    auth: READ_GITHUB_TOKEN
})


const addImage = async (newFiles: addedArt[]) => {
    try {
        await Promise.all(newFiles.map(async (newFile) => {
            await write.request('PUT /repos/iAmIdc/Gallary/contents/images/' + newFile.name, {
                owner: 'iAmIdc',
                repo: 'Gallary',
                path: newFile.name,
                message: 'eddited zrt',
                committer: {
                    name: 'app',
                    email: "test@test.com"
                },
                content: newFile.file,
                headers: {
                    'X-GitHub-Api-Version': '2022-11-28'
                }
            })
        }))
    } catch (error) {
        console.log(error)
    }
}

const deleteImage = async (fileNames: string[]) => {
    try {
        await Promise.all(fileNames.map(async (fileName) => {
            const getSha = await write.request("GET /repos/iAmIdc/Gallary/contents/images/" + fileName,
                {
                    owner: 'iAmIdc',
                    repo: 'Gallary',
                    path: 'index.json'
                }
            );

            await write.request("DELETE /repos/iAmIdc/Gallary/contents/images/" + fileName,
                {
                    owner: 'iAmIdc',
                    repo: 'Gallary',
                    path: 'index.json',
                    message: 'eddited josn',
                    committer: {
                        name: 'app',
                        email: "test@test.com"
                    },
                    sha: getSha.data.sha,
                    headers: {
                        'X-GitHub-Api-Version': '2022-11-28'
                    }
                })
        }))
    } catch (e) {
        console.log(e)
    }
}

const editJson = async (newJson: art[]) => {
    try {

        const getSha = await write.request("GET /repos/iAmIdc/Gallary/contents/index.json", {
                owner: 'iAmIdc',
                repo: 'Gallary',
                path: 'index.json'
            }
        )

        await write.request('PUT /repos/iAmIdc/Gallary/contents/index.json', {
            owner: 'iAmIdc',
            repo: 'Gallary',
            path: 'index.json',
            message: 'eddited josn',
            committer: {
                name: 'app',
                email: "test@test.com"
            },
            content: encode((JSON.stringify(newJson))),
            sha: getSha.data.sha,
            headers: {
                'X-GitHub-Api-Version': '2022-11-28'
            }
        })
    } catch (error) {
        console.log(error)
    }
}

const getJson = async () => {
    try {
        const data = await read.request("GET /repos/iAmIdc/Gallary/contents/index.json", {
                owner: 'iAmIdc',
                repo: 'Gallary',
                path: 'index.json',
            }
        )

        return JSON.parse(decode(data.data.content));
    } catch {
        return ([]);
    }
}

export {getJson, editJson, deleteImage, addImage}